.head_banner{
    background-color:rgb(20, 25, 46)/*rgb(20, 25, 46)*/;
    color:white;
    height:60px;
    width:100%;
    display: flex;
    flex-wrap:nowrap;
    justify-content: space-between;
}
.head_banner_logo{
    background-color:rgb(20, 25, 46);
    /*clip-path: polygon(0 0,170px 0,65px 200px,0 200px);*/
    z-index: 10;
    flex:0 0 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right:10px;
}

.head_banner_logo>svg{
    width:40px;
    height:40px;
}

.head_search{
    flex:0 1 max-content;
    height:100%;
    width:500px;
    display:flex;
    justify-content: flex-end;
    align-items:center;
    position: relative;
}
.head_search_form{
    width:100%;
    display: flex;
    justify-content: flex-end;
    align-self: stretch;
    
    position: relative;
    z-index:0;
}
.head_search_field{
    flex:0 1 75%;
    background-color:inherit;
    outline:none;
    border:0;
    border-bottom:2px solid rgb(71, 84, 134);
    align-self: center;
    height:78%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 100;
    color:white;
    font-size:22px;
    padding-left:10px;
    margin-right:5px;
    -webkit-transition:all 0.3s;
    transition:all 0.3s;
    box-sizing:content-box;
}
.head_search_field:focus{
    border-color:turquoise;
}
.head_search_submit{
    flex:1 1 max-content;
    border:0;
    outline:none;
    background-color:rgb(42, 52, 94);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 100;
    color:white;
    font-size:25px;
}
@media only screen and (max-width:380px){
    .head_banner_logo{
        display: none;
    }
    .head_search_submit{
        font-size:20px;
    }
}
.search_suggest{
    position: absolute;
    top:100%;
    left:0;
    z-index:100;
    width:calc(75% + 10px);
    background-color:white;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    box-shadow:0 2px 15px rgb(0,0,0,0.4);
    padding:5px 20px 8px 20px;
    box-sizing:border-box;
}
.search_suggest_list{
    padding:0;
    margin:0;
    list-style-type:none;
    color:black;
    display: block;
    width:100%;
}
.search_suggest_item{
    padding:8px 25px 8px 25px;
    font-size:20px;
    border-bottom:1.1px solid rgba(36, 48, 94, 0.336);
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight:100;
    cursor:pointer;
    color:rgb(102, 101, 101);
}
.search_suggest_item:last-child{
    border:0;
}
